*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: "DM Sans", sans-serif;
  font-size: 22px;
  line-height: 24px;
  height: 100%;
  display: flex;
  flex-direction: column;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  margin: 0;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.calc-app {
  color: #d8d8d8;
  background-color: #1c1c1c;
}

.calc-app__header {
  width: 100%;
  padding: 30px;
  color: #d8d8d8;
}

.calc-app__logo {
  font-size: 30px;
  line-height: 32px;
}

.calc-app__main {
  flex-grow: 1;
  height: 100%;
}

.calculator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 375px;
  height: 100%;
  margin: 0 auto;
  padding: 25px;
}

.calculator__input-field {
  display: block;
  width: 100%;
  height: 80px;
  margin: 0 0 30px 0;
  padding: 0 10px;
  font-size: 70px;
  line-height: 74px;
  font-family: "DM Sans", sans-serif;
  text-align: right;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0);
  border: none;
}
.calculator__input-field:focus {
  outline: none;
}

.calculator__buttons {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.calculator__row {
  display: grid;
  width: 100%;
  gap: 15px;
  grid-template-columns: repeat(4, 1fr);
}

.calculator__button {
  width: 70px;
  height: 70px;
  margin: 0;
  padding: 0;
  font-size: 28px;
  line-height: 30px;
  font-family: "DM Sans", sans-serif;
  text-align: center;
  border: none;
  border-radius: 35px;
  cursor: pointer;
}

.calculator__button--zero {
  width: 100%;
  grid-column: 1/3;
}

.calculator__button--digit {
  color: #ffffff;
  background-color: #505050;
}

.calculator__button--operator {
  color: #ffffff;
  background-color: #ff9500;
}